@Config('ib-baggages-calculator-app')
class BaggagesAppConfig {
    constructor (private BaggagesAppConstants: BaggagesAppConstantsNamespace.BaggagesAppConstants,
        private $translateProvider: angular.translate.ITranslateProvider,
        private localStorageServiceProvider: angular.local.storage.ILocalStorageServiceProvider,
        private $httpProvider: ng.IHttpProvider, uiSelectConfig: angular.ui.select.ISelectConfig,
        private tmhDynamicLocaleProvider: any) {
        uiSelectConfig.theme = 'bootstrap';
    }
    active = ():void => {
        //Initialize the translation filter provider
        this.$translateProvider.useStaticFilesLoader( { files: [{ prefix: this.BaggagesAppConstants.LABELS_ROUTE, suffix: '.json' }, { prefix: this.BaggagesAppConstants.GENERAL_LABELS_ROUTE, suffix: '.json' }] });
        this.$translateProvider.useSanitizeValueStrategy('escaped');
        this.tmhDynamicLocaleProvider.localeLocationPattern(this.BaggagesAppConstants.LOCALE_ROUTE + '/locale_{{locale}}.js');
        //Initialize the local storage provider
        this.localStorageServiceProvider.setPrefix('ib-baggages-calculator-app');
        this.localStorageServiceProvider.setStorageType('sessionStorage');
        this.$httpProvider.interceptors.push('IberiaHttpInterceptor');
        this.$httpProvider.defaults.withCredentials = true;
    };
    $onInit = this.active();
}
