@Component('ib-baggages-search-result-module', 'ibBaggagesComponentResult', {
    bindings: {
        model: '=',
        modelBaggages: '=',
        view: '=',
        rates: '=',
        typeClient: '<'

    },
    controllerAs: 'bagResultComp',
    templateUrl: ['BaggagesAppConstants', (baggageConstant: BaggagesAppConstantsNamespace.BaggagesAppConstants): string => baggageConstant.TEMPLATES_ROUTE + 'ib-baggages-result-component.html'
    ]
}, ['$scope', '$timeout'], [])

class BaggagesResultComponent implements ng.IController {
    model: IbBaggagesResult.IbBaggagesResultModel;
    olderSelectedRate: any = '';
    olderNumberBaggages: any = '';
    form: any;

    constructor (private $scope: ng.IScope, private $timeout: ng.ITimeoutService) {
        this.$scope.$on('resetForm', this.resetForm);
    }
    calculateBaggagesOfRate = () => {
        this.form.$submitted = true;

        if (this.form.$valid) {
            if (this.olderSelectedRate !== this.model.selectedRate || this.olderNumberBaggages !== this.model.numberBaggages) {
                this.olderSelectedRate = this.model.selectedRate;
                this.olderNumberBaggages = this.model.numberBaggages;

                this.$scope.$emit(
                    'calculateBaggagesOfRate',
                    this.model.selectedRate,
                    this.model.numberBaggages
                );

                this.$timeout(() => {
                    if (angular.element('#baggagesMobile').is(':visible')) {
                        angular.element('html, body').animate({
                            scrollTop: angular.element('#baggagesMobile').offset().top - 200
                        });
                    }
                });
            }
        }
    };

    updateNumberBaggages = () => {
        this.$scope.$emit('calculateNumberBaggages', this.model.selectedRate);
        this.resetForm();
    };

    resetForm = () => {
        this.form.$submitted = false;
    };
}