@Service('ib-baggages-services-module', [
    'BaggagesUtilService', 'CurrencyService', 'localStorageService', 'BaggagesSearchConstants'])

class BaggagesSearchTransformer {

    constructor (
        private BaggagesUtilService: BaggagesUtilService,
        private CurrencyService: any,
        private localStorageService: angular.local.storage.ILocalStorageService,
        private BaggagesSearchConstants: BaggagesSearchConstantsNamespace.BaggagesSearchConstants
    ) { }

    /**
    * Create the structuree of city for show in combo of city origin and city destination
    */
    initializeBaggagesSearchModel = (cities: any[], cabins, cardPlus) => {
        /**
         * Order levels of iberia plus cards
         */

        const citiesModel = { listGroups: [], listCities: [] };
        const listCardPlus = [];

        for (let i = 0; i < cities.length; i++) {
            citiesModel.listGroups.push(cities[i].description);
            for (let k = 0; k < cities[i].cities.length; k++) {
                const city = { code: cities[i].cities[k].code, description: cities[i].cities[k].description, country: cities[i].cities[k].country.code, group: cities[i].description };
                citiesModel.listCities.push(city);
            }
        }

        const orderLevels = () => {
            let level;
            listCardPlus.push({ name: 'IBERIA_PLUS_NOT_CLIENT_TEXT', isDefault: true });
            for (let i = 0; i < this.BaggagesSearchConstants.PLUS_CARDS.length; i++) {
                let k = 0;
                let isFound = false;
                while (!isFound && k < cardPlus.length) {
                    if (cardPlus[k].name === this.BaggagesSearchConstants.PLUS_CARDS[i]) {
                        level = cardPlus[k];
                        level.isDefault = false;
                        listCardPlus.push(level);
                        cardPlus.splice(k, 1);
                        isFound = true;
                    }
                    k++;
                }
            }
            if (cardPlus.length > 0) {
                for (let i = 0; i < cardPlus.length; i++) {
                    level = cardPlus[i];
                    level.isDefault = false;
                    listCardPlus.push(level);
                }
            }
        };
        orderLevels();

        return { cities: citiesModel, cabins: cabins, plusClients: listCardPlus };
    };

    /**
    * Create the structure of baggages when user select number of baggages and rate.
    * @param {Array} List with all fareFamlies
    * @param {String} fareSelected  Fare selected in combo view
    * @param {Number} Number of baggages for delimit search
    * @return {Object} Contain separated price for number of baggages and total price for web and airport
    */
    generateCalculateBaggages = (fareFamilies: IbBaggagesResult.IbBaggagesResultModelFareFamilies[], fareSelected: string, number: number) => {
        interface TransformedData {
            listBaggages: any[],
            totalWeb: any,
            totalAirPort: any
        }

        let i = 0, isFounded = false, numBagsInterval: number, numBag = 1,  baggageCounter = 0;
        const currency = this.localStorageService.get<CommonsCurrencyModel.CurrencyResponse>('currency');
        const transformedData: TransformedData = { listBaggages: [], totalWeb: 0, totalAirPort: 0 };
        let bagData = { pieces: 0, priceAirport: '', priceWeb: '' };
        let myFareIntervals = [];

        while (!isFounded && i < fareFamilies.length) {
            if (fareFamilies[i].name === fareSelected) {
                isFounded = true;
                myFareIntervals = fareFamilies[i].baggages;
            }
            i++;
        }

        while (numBag <= number) {
            for (i = 0; i < myFareIntervals.length; i++) {
                numBagsInterval = 1;
                if (numBag <= number) {
                    baggageCounter = (myFareIntervals[i].lastPc - myFareIntervals[i].firstPc) + 1;
                    while (numBagsInterval <= baggageCounter && numBag <= number) {
                        bagData = {
                            pieces: numBagsInterval,
                            priceWeb: '',
                            priceAirport: ''
                        };
                        numBagsInterval++;
                        numBag++;
                    }
                    bagData.priceWeb = this.CurrencyService.formatPrice(currency, myFareIntervals[i].web.price, false, false);
                    bagData.priceAirport = this.CurrencyService.formatPrice(currency, myFareIntervals[i].airport.price, false, false);

                    transformedData.totalAirPort += myFareIntervals[i].airport.price * bagData.pieces;
                    transformedData.totalWeb += myFareIntervals[i].web.price * bagData.pieces;
                    transformedData.listBaggages.push(bagData);
                }
            }
        }
        transformedData.totalWeb = this.CurrencyService.formatPrice(currency, transformedData.totalWeb, false, false);
        transformedData.totalAirPort = this.CurrencyService.formatPrice(currency, transformedData.totalAirPort, false, false);

        return transformedData;
    };

    /**
    * Return the fare families of service with format for show in view
    * @param {Object} baggageInfo Fare famlies with rate of baggages
    */
    transformBaggagesView = (baggageInfo: any): any => {

        const getFranchises = (fare: any): any => {
            franchise = { holdBaggage: { freePc: !(fare.fba.freePC < 1), numberPc: fare.fba.freePC }, handBagge: { freePc:!(fare.carryOn.freePC < 1), numberPc: fare.carryOn.freePC } };
            return franchise;
        };

        const getBaggages = (baggages: any[]): any[] => {
            const baggagesArray = [];

            for (k = 0; k < baggages.length; k++) {
                const baggage = { bagNum: baggages[k].bagNum, firstPc: baggages[k].firstPC, lastPc: baggages[k].lastPC, airport: { price: baggages[k].feeAmountCKI }, web: { price: baggages[k].feeAmountWEB }, description: baggages[k].commercialName };
                baggagesArray.push(baggage);
            }
            return baggagesArray;
        };

        let i, k, franchise;
        const transformedInfo = { rates: [], baggagesTransformed: { fareFamilies: [] } };
        for (i = 0; i < baggageInfo.fareFamilies.length; i++) {
            const family = {
                name: baggageInfo.fareFamilies[i].name, franchise: getFranchises(baggageInfo.fareFamilies[i].franchise), baggages: getBaggages(baggageInfo.fareFamilies[i].baggages)
            };
            transformedInfo.rates.push(baggageInfo.fareFamilies[i].name);
            transformedInfo.baggagesTransformed.fareFamilies.push(family);
        }
        return transformedInfo;
    };

    /**
    * Create the request to send to serive
    * @param {Object} model Data input of user in view
    */
    generateBaggagesRequest = (model: any): IbBaggagesResult.RequestBaggage => {
        const requestBaggages: IbBaggagesResult.RequestBaggage = {
            origin: model.cityOrigin.code,
            destination: model.cityDestiny.code,
            flightDate: this.BaggagesUtilService.getTransformedDate(model.dateFlight),
            buyDate: this.BaggagesUtilService.getTransformedDate(model.buyFlightDate),
            topTier: model.typeClient && !model.typeClient.isDefault ? model.typeClient.topTierLevel : null,
            cabinClassType: model.cabin.cabinClass,
            carrier: 'IB'
        };
        return requestBaggages;
    };
}