@Component('ib-baggages-search-engine-module', 'ibBaggagesSearchEngine', {
    bindings: {
        searchModel: '=',
        typeCabins: '<',
        typeClients: '=',
        containerForm: '=',
        view: '=',
        modelCities: '=',
        isLoading: '='
    },
    controllerAs: 'bagSearchEngineCtrl',
    templateUrl: ['BaggagesAppConstants', (baggageConstant: BaggagesAppConstantsNamespace.BaggagesAppConstants): string => `${baggageConstant.TEMPLATES_ROUTE}ib-baggages-search-engine.html`
    ]
}, ['$scope'], [])
class BaggagesSearchEngineController implements ng.IController {
    searchModel: any;
    typeCabins: any;
    typeClients: any;
    containerForm: any;
    view: any;
    modelCities: any;
    isLoading: boolean;
    optionsBuyFlightDate: any = {
        showWeeks: false,
        minDate: 0,
        maxDate: 0
    };
    optionsDateFlight: any = {
        showWeeks: false,
        minDate: 0,
        maxDate: 0
    };
    dateOptions: any = {
        showWeeks: false,
        maxMode: ''
    };
    dateFligthPickerOpened: boolean;
    dateBuyPickerOpened: boolean;
    dataFounded: boolean;

    constructor (
        private $scope: ng.IScope
    ) { }

    $onInit = (): void => {
        this.dateOptions = {
            showWeeks: false,
            maxMode: 'day'
        };
        this.searchModel.stateBuyDate = true;
        this.dateFligthPickerOpened = false;
        this.dateBuyPickerOpened = false;
        this.dataFounded = false;

        this.optionsBuyFlightDate = {
            showWeeks: false,
            minDate: this.getPreviousYear()
        };
        this.optionsDateFlight = {
            showWeeks: false,
            minDate: new Date(),
            maxDate: this.getNextYear()
        };
    };

    /**
    * Update maxDate of buy date of fligth
    */
    updateDateOfBuyFlight = (): void => {
        this.searchModel.stateBuyDate = false;
        this.optionsBuyFlightDate.maxDate = this.searchModel.dateFlight;
        if (this.searchModel.buyFlightDate) {
            if (this.searchModel.dateFlight.getDate() < this.searchModel.buyFlightDate.getDate()) {
                this.searchModel.buyFlightDate = null;
            }
        }
    };

    /**
    * Return date subtracting a year
    */
    getPreviousYear = (): number => {
        const date = new Date();
        return date.setFullYear(date.getFullYear() - 1);
    };

    /**
    * Return date adding two years
    */
    getNextYear = (): number => {
        const date = new Date();
        return date.setFullYear(date.getFullYear() + 2);
    };

    /**
     * Emits event for send info to service
     */
    completeForm = (): void => {
        this.$scope.$emit('completeBaggageFormEvent');
    };

    /**
     * Emits event for calculate other rate.
     */
    newCalculate = (): void => {
        this.$scope.$emit('calculateNewRate');
    };
}