@Service('ib-baggages-services-module', [
    '$cookies',
    '$window',
    'BaggagesAppConstants',
    'BaggagesUtilService',
    'tmhDynamicLocale',
    '$rootScope'
], [])
class LanguageAndMarketService {
    constructor (
        private $cookies: angular.cookies.ICookiesService,
        private $window: ng.IWindowService,
        private BaggagesAppConstants: BaggagesAppConstantsNamespace.BaggagesAppConstants,
        private BaggagesUtilService: BaggagesUtilService,
        private tmhDynamicLocale: any,
        private $rootScope: CommonsRootScope
    ) {}

    init = () => {
        const langCookie: any = this.$cookies.get(this.BaggagesAppConstants.COOKIES.PERSONALIZATION);
        const langUrl: string = this.BaggagesUtilService.getParameterByName(this.BaggagesAppConstants.NAME_PARAMETERS.LANG);
        const langUrlMarket: string = this.BaggagesUtilService.getParameterByName(this.BaggagesAppConstants.NAME_PARAMETERS.MARKET);
        // @ts-ignore
        let langNav: string = this.$window.navigator.language || this.$window.navigator.browserLanguage;
        let lang: string = '';
        let market: string = '';
        let content: string = null;
        let expiration: string | Date = null;
        const jsonCookies: any = {
            expires: '',
            path: ''
        };

        langNav = langNav && langNav.split('-')[0];
        if (angular.isDefined(langUrl) && langUrl !== null) {
            lang = langUrl.toLowerCase();
        } else if (angular.isDefined(langCookie)) {
            lang = langCookie.split('|')[2];
        } else if (angular.isDefined(langNav)) {
            lang = langNav.toLowerCase();
        } else {
            lang = this.BaggagesAppConstants.DEFAULT_LANG;
        }

        if (angular.isDefined(langUrlMarket) && langUrlMarket !== null) {
            market = langUrlMarket.toUpperCase();
        } else if (angular.isDefined(langCookie)) {
            market = langCookie.split('|')[1];
        } else {
            market = this.BaggagesAppConstants.DEFAULT_MARKET;
        }

        if (this.BaggagesAppConstants.AVAILABLE_LANGUAGES.indexOf(lang) === -1) {
            lang = this.BaggagesAppConstants.DEFAULT_LANG;
        }

        content = market + '|' + market + '|' + lang + '| | |' + market + '|';
        expiration = new Date();

        expiration.setTime(
            expiration.getTime() + this.BaggagesAppConstants.COOKIES.DEFAULT_EXPIRY_TIME
        );

        jsonCookies.expires = expiration;
        jsonCookies.path = this.BaggagesAppConstants.COOKIES.DEFAULT_PATH;

        this.$cookies.put(this.BaggagesAppConstants.COOKIES.PERSONALIZATION, content, jsonCookies);

        this.BaggagesAppConstants.MARKET = market;
        this.BaggagesAppConstants.LANG = lang;

        this.$rootScope.appLang = this.BaggagesAppConstants.LANG;
        this.$rootScope.appMarket = this.BaggagesAppConstants.MARKET;
        this.tmhDynamicLocale.set(lang);
    };
}
