@Service('ib-http-services-commercial-module', ['$q', 'HttpUtilsService', 'CommonsCommercialSellingDataUtils', 'CommonsHttpUtilsErrorService', 'CommonsGenericProxy', 'localStorageService'])

class CommercialInfobyMarketService {

    public NUM_ATTEMPS: number;
    constructor (
        private $q: ng.IQService,
        private HttpUtilService: HttpUtilsService,
        private CommonsCommercialSellingDataUtils: any,
        private HttpUtilsErrorService: any,
        private CommonsGenericProxy: any,
        private localStorageService: angular.local.storage.ILocalStorageService
    ) {
        this.NUM_ATTEMPS = 0;
    }

    getCommercialInfo = () => {
        let defer = this.$q.defer();

        const getCommercialInfoSuccess = (commercialData: any): void => {
            this.localStorageService.set('commercialData', this.CommonsCommercialSellingDataUtils.getCommercialsellingDataByMarket(commercialData.data));
            defer.resolve(commercialData);
            defer = null;
            this.NUM_ATTEMPS = 0;
        };

        const getCommercialInfoCallback = () => {
            this.NUM_ATTEMPS++;
            this.getCommercialInfo();
        };

        const getCommercialInfoError = (error: CommonsErrorsModel.ErrorDataGeneral): void => {
            switch (error.errorCode) {
                    case 408: this.HttpUtilsErrorService.returnProcessedError(this.HttpUtilsErrorService.timeOutError(this.NUM_ATTEMPS, getCommercialInfoCallback, error), { defer: defer }); break;
                    case 401: this.HttpUtilsErrorService.returnProcessedError(this.HttpUtilsErrorService.unauthorizedError(this.NUM_ATTEMPS, getCommercialInfoCallback, error), { defer: defer }); break;
                    default: this.HttpUtilsErrorService.returnProcessedError(this.HttpUtilsErrorService.processGeneralError(error), { defer: defer });
            }
        };

        const endpoint = this.HttpUtilService.buildServiceEndpoint('rdu-loc', 'COMMERCIAL_SELLING');
        this.CommonsGenericProxy.getGeneric(endpoint).then(getCommercialInfoSuccess, getCommercialInfoError);

        return defer.promise;
    };
}