@Config('ib-baggages-routing-module')
class RouterConfig {

    constructor (private $routeProvider: any, private BaggagesRouterConstants: BaggageRouterConstants) {

    }

    active = (): void => {
        this.$routeProvider.
            when(this.BaggagesRouterConstants.PAGES_URL.BAGGAGES_SEARCH, {
                templateUrl: this.BaggagesRouterConstants.TEMPLATES_ROUTE + this.BaggagesRouterConstants.BAGGAGES_SEARCH_ROUTE,
                controller: 'BaggagesSearchController',
                controllerAs: 'baggagesCtrl',
                reloadOnSearch: false
            }).otherwise({ redirectTo: this.BaggagesRouterConstants.PAGES_URL.BAGGAGES_SEARCH });
    };
    $onInit = this.active();

}
