@Service('ib-http-services-ibplus-module',
    ['$q', 'CommonsGenericProxy', 'CommonsHttpUtilsErrorService', 'CommonsHttpServicesConstants', 'localStorageService'])

class IberiaPlusCardLevelsService {

    private counterAttempts: number = 0;

    constructor (private $q: ng.IQService,
        private CommonsGenericProxy: CommonsGenericProxy,
        private CommonsHttpUtilsErrorService: CommonsHttpUtilsErrorService,
        private CommonsHttpServicesConstants: CommonsConstantsHttpNamespace.CommonsHttpServicesConstants,
        private localStorageService: angular.local.storage.ILocalStorageService) { }

    getIberiaPlusCardLevels = (): angular.IPromise<any> => {
        let defer = this.$q.defer();
        const levels = this.localStorageService.get('levels');

        const getIberiaPlusCardLevelsSuccess = (data): void => {
            this.localStorageService.set('levels', data.data);
            defer.resolve(data.data);
            defer = null;
            this.counterAttempts = 0;
        };

        const getIberiaPlusCardLevelsError = (error): void => {
            this.CommonsHttpUtilsErrorService.controlError(error, this.counterAttempts, getIberiaPlusCardLevelsCallback, defer, false);
        };

        const  getIberiaPlusCardLevelsCallback = (): void => {
            this.counterAttempts++;
            this.getIberiaPlusCardLevels();
        };

        if (levels === null) {
            const endpoint = this.CommonsHttpServicesConstants.DEFAULT_ENDPOINTS.ibPlusLevelsServiceURI;
            this.CommonsGenericProxy.getGeneric(endpoint).then(getIberiaPlusCardLevelsSuccess, getIberiaPlusCardLevelsError);
        } else {
            defer.resolve(levels);
        }

        return defer.promise;
    };
}