class InitRouter {
    //We are using incorrectly angular-route library, $state.go is a function from angular-ui-router
    $state: ng.route.IRoute & { go: any };
    preconditionEvent: any;

    constructor (private $rootScope: CommonsRootScope, private CommonsIberiaRouterPreconditionsService: CommonsIberiaRouterPreconditionsService, private $window: ng.IWindowService) {

    }

    $onInit = (): void => {
        this.preconditionEvent = this.$rootScope.$on('$routeChangeStart', (event, toState, toParams) => {
            /* istanbul ignore next */
            if (!this.CommonsIberiaRouterPreconditionsService.canTransition(toState.name, toParams)) {
                //Stop the state transition from working
                event.preventDefault();
                //Navigate to a otherwise state OR reload the app
                if (this.CommonsIberiaRouterPreconditionsService.otherwise(toState.name)) {
                    this.$state.go(this.CommonsIberiaRouterPreconditionsService.otherwise(toState.name));
                } else {
                    this.$window.location.href = this.$window.location.href.split('#')[0]; // Application is in a unrecoverable state reload
                }
            }
        });
    };
}



angular.module('ib-baggages-routing-module', [
    'ngRoute'
]);
angular.module('ib-baggages-routing-module').run(InitRouter);
