@Service(
    'ib-baggages-services-module',
    ['$window', 'localStorageService'],
    []
)
class BaggagesUtilService {
    constructor (
        private $window: ng.IWindowService,
        private localStorageService: angular.local.storage.ILocalStorageService
    ) { }

    getDeviceType = (): string => {
        let width: any = null;
        let deviceType: string = '';
        const channel: string = this.localStorageService.get('channel');

        if (channel === 'mobi') {
            deviceType = 'mobile';
        } else {
            width = this.$window.innerWidth;

            if (width !== null && width !== undefined) {
                if (width > 1024) {
                    deviceType = 'desktop';
                } else {
                    if (width > 480 && width < 1025) {
                        deviceType = 'tablet';
                    } else {
                        if (width < 481) {
                            deviceType = 'mobile';
                        }
                    }
                }
            }
        }

        return deviceType;
    };

    getTransformedDate = (date: Date): string => {
        const result = `${ date.getFullYear() }-${ date.getMonth() + 1 }-${ date.getDate() }`;
        return result;
    };

    initStorage = (): void => {
        this.localStorageService.remove('loggedToken');
        this.localStorageService.remove('refreshToken');
        this.localStorageService.remove('customerData');
        this.localStorageService.remove('userLogged');
    };

    getParameterByName = (name: string, url?: string): null | string => {
        let regex: any = null;
        let results: any = null;

        if (url === undefined) {
            url = this.$window.location.href;
        }

        name = name.replace(/[\[\]]/g, '\\$&');
        regex = new RegExp('[?&]' + name + '(=([^&#]*)|&|#|$)');
        results = regex.exec(url);

        if (!results) {
            return null;
        }

        if (!results[2]) {
            return null;
        }

        return decodeURIComponent(results[2].replace(/\+/g, ' '));
    };

    /*getBackGroundURL = (): any => {
    let backgroundURL = null;
    let widthScreen = this.$window.innerWidth > 1024 ? 2000 : this.$window.innerWidth > 480 && this.$window.innerWidth < 1025 ? 768 : this.$window.innerWidth < 481 ? 320 : 2000;
    backgroundURL = this.BaggagesAppConstants.backgroundImagesBaseRoute + this.BaggagesAppConstants.defaultBackgroundImageWidth + widthScreen + '.jpg';
    return backgroundURL;
  };*/
}