@Service('ib-http-services-cities-module', ['$q', 'HttpUtilsService', 'CommonsHttpUtilsErrorService', 'CommonsGenericProxy'])
class BaggagesGetDestinationAreasCitiesListService {

    public NUM_ATTEMPS: number;
    constructor (
        private $q: ng.IQService,
        private HttpUtilService: HttpUtilsService,
        private HttpUtilsErrorService: any,
        private CommonsGenericProxy: any
    ) {
        this.NUM_ATTEMPS = 0;
    }

    getDestinationAreasCitiesList = (type: string = undefined, areas: string = undefined): angular.IPromise<any> => {
        let defer = this.$q.defer();

        const getDestinationAreaCitiesListSuccess = (destinationCities: any): void => {
            defer.resolve(destinationCities.data);
            defer = null;
            this.NUM_ATTEMPS = 0;
        };

        const getDestinationCitiesListCallback = () => {
            this.NUM_ATTEMPS++;
            this.getDestinationAreasCitiesList(type, areas);
        };

        const getDestinationAreaCitiesListError = (error: CommonsErrorsModel.ErrorDataGeneral): void => {
            switch (error.errorCode) {
                    case 408: this.HttpUtilsErrorService.returnProcessedError(this.HttpUtilsErrorService.timeOutError(this.NUM_ATTEMPS, getDestinationCitiesListCallback, error), {defer}); break;
                    case 401: this.HttpUtilsErrorService.returnProcessedError(this.HttpUtilsErrorService.unauthorizedError(this.NUM_ATTEMPS, getDestinationCitiesListCallback, error), {defer}); break;
                    default: this.HttpUtilsErrorService.returnProcessedError(this.HttpUtilsErrorService.processGeneralError(error), {defer});
            }
        };

        const endpoint = this.HttpUtilService.buildServiceEndpoint('rdu-loc', 'DESTINATION_AREAS') + (type ? '?type=' + type : '') + (areas ? '&areas=' + areas : '');
        this.CommonsGenericProxy.getGeneric(endpoint).then(getDestinationAreaCitiesListSuccess, getDestinationAreaCitiesListError);
        return defer.promise;
    };
}