@Service('ib-http-services-utils-module', ['localStorageService', 'HttpServicesConstants', 'BaggagesAppConstants'])

class HttpUtilsService {
    constructor (
        private localStorageService: angular.local.storage.ILocalStorageService,
        private HttpServicesConstants: HttpServicesConstantsNamespace.HttpServicesConstants,
        private BaggagesAppConstants: BaggagesAppConstantsNamespace.BaggagesAppConstants
    ) { }

    buildServiceEndpoint = (domain: string, endpointAttribute: string) => {
        const serviceBaseEnpoint = this.getEndpoint(domain + '-rs');
        return serviceBaseEnpoint ? serviceBaseEnpoint + this.HttpServicesConstants.URI[endpointAttribute] :
            this.BaggagesAppConstants.SERVICE_URI + this.HttpServicesConstants.URI.DEFAULT_ENDPOINTS[endpointAttribute];
    };

    getEndpoint = (key: string) => {
        return (this.localStorageService.get('endpoints') || {})[key];
    };
}
