@Service('ib-baggages-services-module', [
    '$q', 'BaggagesSearchService', 'GetDestinationsService', 'BaggagesSearchTransformer',
    'CabinTypesService', 'IberiaPlusCardLevelsService', 'localStorageService', 'CurrencyService',
    'DatalayerUtilsService', 'CommonsGetLoginInfoService'])

class BaggagesService {

    constructor (
        private $q: ng.IQService,
        private BaggagesSearchService: BaggagesSearchService,
        private GetDestinationsService: GetDestinationsService,
        private BaggagesSearchTransformer: BaggagesSearchTransformer,
        private CabinTypesService: CabinTypesService,
        private IberiaPlusCardLevelsService: IberiaPlusCardLevelsService,
        private localStorageService: angular.local.storage.ILocalStorageService,
        private CurrencyService: CurrencyService,
        private DatalayerUtilsService: DatalayerUtilsService,
        private CommonsGetLoginInfoService: CommonsGetLoginInfoService
    ) { }

    /**
    * Init page of baggage search
    */
    initBaggageCalculator = (): angular.IPromise<any> => {
        const initPromise = this.$q.defer();
        let cabins, iberiaPlusCards;

        const showServiceError = (error) => {
            initPromise.reject(error);
        };

        const getDestinationSuccess = (iberiaPlusCardsData) => {
            iberiaPlusCards = iberiaPlusCardsData;
            this.GetDestinationsService.getDestinationAreasCitiesList().then(
                (response) => {
                    initPromise.resolve(this.BaggagesSearchTransformer.initializeBaggagesSearchModel(response, cabins, iberiaPlusCards));
                }, showServiceError);
        };


        const getIberiaPlusCardSuccess = (cabinsData) => {
            cabins = cabinsData;
            this.IberiaPlusCardLevelsService.getIberiaPlusCardLevels().then(getDestinationSuccess, showServiceError);
        };

        const afterTokenSuccess = () => {
            this.DatalayerUtilsService.updateLoggedDatalayer(this.localStorageService.get('customerData'));
            this.CabinTypesService.getCabinTypes().then(getIberiaPlusCardSuccess, showServiceError);
        };

        this.CommonsGetLoginInfoService.checkCustomerStatus(true).then(afterTokenSuccess, showServiceError);

        return initPromise.promise;
    };

    /**
    * Get baggages with params inputs of user in form
    * @param {Object} data Structure for send to service
    */
    getBaggages = (data): angular.IPromise<any> => {

        const getBaggagesSuccess = (data) => {
            this.localStorageService.set('currency', this.CurrencyService.getCurrencyObject(data.currency));
            initPromise.resolve(this.BaggagesSearchTransformer.transformBaggagesView(data));
        };

        const getBaggagesError = (error) => initPromise.reject(error);

        const initPromise = this.$q.defer();
        this.BaggagesSearchService.getBaggages(this.BaggagesSearchTransformer.generateBaggagesRequest(data)).then(getBaggagesSuccess, getBaggagesError);

        return initPromise.promise;
    };

    /**
    * Calculate price of baggages with number of baggage and type of cabin selected
    * @param {String} cabin Type of cabin selected for show price
    * @param {String} numberBaggages Number of baggage to show  price
    */
    getBaggagesCalculated = (fareFamilies: any, cabin: string, numberBaggages: number): any => this.BaggagesSearchTransformer.generateCalculateBaggages(fareFamilies, cabin, numberBaggages);

}