@Controller('ib-baggages-search-module', ['$scope', '$timeout', 'localStorageService', 'CommonsModalService',
    'BaggagesService', 'BaggagesSearchConstants'], [])
class BaggagesSearchController implements ng.IController {

    public model;
    public form;

    constructor (
        private $scope: ng.IScope,
        private $timeout: ng.ITimeoutService,
        private localStorageService: angular.local.storage.ILocalStorageService,
        private ModalService: any,
        private BaggagesService: BaggagesService,
        private BaggagesSearchConstants: BaggagesSearchConstantsNamespace.BaggagesSearchConstants
    ) {
        this.model = this.BaggagesSearchConstants.MODEL;
        this.$scope.$on('completeBaggageFormEvent', this.sendSearchInfo);
        this.$scope.$on('calculateNewRate', this.newCalculate);
        this.$scope.$on('calculateBaggagesOfRate', this.calculateBaggages);
        this.$scope.$on('citySelectorType', this.updateCityAndDestiny);
        this.$scope.$on('calculateNumberBaggages', this.calculateNumberBaggages);
        this.$onInit();
    }

    $onInit = () => {
        if (this.localStorageService.isSupported) {
            this.BaggagesService.initBaggageCalculator().then((modelInitialized) => {
                this.model.cities = modelInitialized.cities;
                this.model.typeCabins = modelInitialized.cabins;
                this.model.typePlusClient = modelInitialized.plusClients;
                this.model.view.isLoading = false;
            }, (error) => {
                this.model.view.isLoading = false;
                this.ModalService.showGenericError(error, true, null).result.then(angular.noop, angular.noop);
            });
        } else {
            const storageNotSuported = {
                title: { message: 'PRIVATE_BROWSER_DETECTED_TITLE', needsTranslate: true },
                needsTranslate: true, hideFooter: true, hideClose: true, message: 'PRIVATE_BROWSER_DETECTED_BODY'
            };
            this.ModalService.showGenericError(storageNotSuported, true, null).result.then(angular.noop, angular.noop);
        }
    };

    sendSearchInfo = (event: angular.IAngularEvent) => {
        this.form.$submitted = true;
        if (this.form.$valid) {
            this.model.view.isLoading = true;
            this.BaggagesService.getBaggages(this.model.searchModel).then((response) => {
                this.model.view.dataFounded = true;
                this.model.view.haveBaggages = true;
                this.model.view.isLoading = false;
                this.model.resultModel = response.baggagesTransformed;
                this.model.rates = response.rates;
            }, (error) => {
                this.model.view.isLoading = false;
                this.ModalService.showGenericError(error, true, null).result.then(angular.noop, angular.noop);
            });
        }
    };

    newCalculate = (event: angular.IAngularEvent) => {
        this.model.view.dataFounded = false;
        this.model.view.haveBaggages = false;
        this.model.resultModel = null;
        this.model.modelBaggages = null;
        this.model.selectedRate = null;
        this.model.numberBaggages = null;
        this.form.$submitted = false;
        this.$scope.$broadcast('resetForm');
    };

    calculateBaggages = (event: angular.IAngularEvent, rate: string, numberBaggages: number) => {
        this.model.modelBaggages = this.BaggagesService.getBaggagesCalculated(this.model.resultModel.fareFamilies, rate, numberBaggages);
    };

    updateCityAndDestiny = (event: angular.IAngularEvent, type: string) => {
        this.$timeout(() => {
            if (this.model.searchModel.cityOrigin && this.model.searchModel.cityDestiny) {
                if (type === this.BaggagesSearchConstants.TYPE_CITY.ORIGIN) {
                    if (this.model.searchModel.cityDestiny.code === this.model.searchModel.cityOrigin.code) {
                        this.model.searchModel.cityDestiny = null;
                        this.form.selector_city_1.submitted = false;
                    }
                } else if (type === this.BaggagesSearchConstants.TYPE_CITY.DESTINY) {
                    if (this.model.searchModel.cityDestiny.code === this.model.searchModel.cityOrigin.code) {
                        this.model.searchModel.cityOrigin = null;
                        this.form.selector_city_0.submitted = false;
                    }
                }
            }
        });
    };

    calculateNumberBaggages = (event: angular.IAngularEvent, rate: string) => {
        let totalBaggages = 0;
        this.model.view.numberMaxOfBaggages = [1, 2, 3, 4, 5, 6, 7, 8, 9];
        totalBaggages = this.model.view.numberMaxOfBaggages.length;
        angular.forEach(this.model.resultModel.fareFamilies, (familie) => {
            if (familie.name === rate) {
                totalBaggages -= familie.franchise.holdBaggage.numberPc;
                this.model.view.numberMaxOfBaggages.splice(totalBaggages, this.model.view.numberMaxOfBaggages.length);
            }
        });
        this.model.resultModel.numberBaggages = null;
        if (this.model.modelBaggages && this.model.modelBaggages.listBaggages) {
            this.model.modelBaggages.listBaggages = null;
        }
    };
}