
class InitApp {
    localObject;
    constructor ($translate: angular.translate.ITranslateService,
        private $rootScope: CommonsRootScope,
        private BaggagesAppConstants: BaggagesAppConstantsNamespace.BaggagesAppConstants,
        private CommonsLanguageAndMarketService: CommonsLanguageAndMarketService,
        private DatalayerUtilsService: DatalayerUtilsService,
        private BaggagesUtilService: BaggagesUtilService,
        private IbCLinksService: IbCLinksService) {
        //Initilize language and market
        this.localObject = this.CommonsLanguageAndMarketService.init();
        this.BaggagesAppConstants.LANG = this.localObject.language;
        this.BaggagesAppConstants.MARKET = this.localObject.market;
        this.$rootScope.appLang = this.BaggagesAppConstants.LANG;
        this.$rootScope.appMarket = this.BaggagesAppConstants.MARKET;
        //Set translation language to use
        $translate.use(this.BaggagesAppConstants.LANG);
        this.$rootScope.routes = {
            icons: this.BaggagesAppConstants.ICONS_ROUTE,
            images: this.BaggagesAppConstants.IMAGES_ROUTE,
            templates: this.BaggagesAppConstants.TEMPLATES_ROUTE,
            links: this.BaggagesAppConstants.LINKS_ROUTE
        };
        this.IbCLinksService.getLinks(true, 'BCA');
        this.DatalayerUtilsService.generateDatalayerDOM;
        this.DatalayerUtilsService.initializeDatalayer(this.BaggagesAppConstants.LANG, this.BaggagesAppConstants.MARKET, this.BaggagesUtilService.getDeviceType());
    }
}

