@Service('ib-sso-service-module', [
    'BaggagesUtilService',
    'localStorageService',
    '$cookies',
    'SSOConstants',
    '$rootScope',
    'CommonsCookiesService'
])
class SSOUtils {
    constructor (
        private BaggagesUtilService: BaggagesUtilService,
        private localStorageService: angular.local.storage.ILocalStorageService,
        private $cookies: angular.cookies.ICookiesService,
        private BaggagesSSOConstants:  SSOConstantsNamespace.SSOConstants,
        private $rootScope: CommonsRootScope,
        private CommonsCookiesService: any
    ) {}
    /**
     * Creates the SSO information, checking the SSO cookies. It decides if the user is logged or not, and saving the token information in
     * local storage.
     * @returns {Object}. The SSO information (if user is logged, the tokens, if there is an SSO error)
     */
    generateSSOInfo = () => {
        const cookiesInformation = this.checkCookiesInformation();
        const errorCode = this.BaggagesUtilService.getParameterByName(this.BaggagesSSOConstants.URL_PARAMETERS.ERROR);
        if(!errorCode) {
            this.localStorageService.set('loggedToken', cookiesInformation.cookieAccessToken);
            this.localStorageService.set('refreshToken', cookiesInformation.cookieRefreshToken);
        }
        this.localStorageService.set('userLogged', cookiesInformation.userLogged);
        return {
            isLogged: cookiesInformation.userLogged,
            loggedToken: cookiesInformation.cookieAccessToken,
            refreshToken: cookiesInformation.cookieRefreshToken,
            needsRefreshToken: this.isTokenExpired(cookiesInformation.cookieAccessTokenExpire),
            error: this.generateSSOErrorMessage(errorCode)
        };
    };
    /**
     * checks the SSO cookies information and returns its value and if the user is logged or not.
     * @returns {Object} the SSO cookies values and if the user is logged or not
     */
    checkCookiesInformation = () => {
        const cookieAccessToken = this.CommonsCookiesService.getCookie(this.BaggagesSSOConstants.SSO_COOKIES.LOGGED_TOKEN);
        const cookieAccessTokenExpire = this.CommonsCookiesService.getCookie(this.BaggagesSSOConstants.SSO_COOKIES.LOGGED_TOKEN_EXPIRATION);
        const cookieRefreshToken = this.CommonsCookiesService.getCookie(this.BaggagesSSOConstants.SSO_COOKIES.LOGGED_REFRESH_TOKEN);
        const cookieRefreshTokenExpire = this.CommonsCookiesService.getCookie(this.BaggagesSSOConstants.SSO_COOKIES.LOGGED_REFRESH_TOKEN_EXPIRATION);

        return {
            userLogged: angular.isDefined(cookieRefreshToken) && !this.isTokenExpired(cookieRefreshTokenExpire),
            cookieAccessToken: cookieAccessToken,
            cookieAccessTokenExpire: cookieAccessTokenExpire,
            cookieRefreshToken: cookieRefreshToken,
            cookieRefreshTokenExpire: cookieRefreshTokenExpire
        };
    };
    /**
     * checks if the token has expired or not
     * @param {String} tokenExpiration the token expiration date in format DD/MM/YYYY HH:mm
     * @returns {Boolean} true if the tokens has espired, its token expiration date is before current date
     */
    isTokenExpired = (tokenExpiration) => {
        let ok = true;
        if (tokenExpiration) {
            const tokenSplit = tokenExpiration.split('/');
            tokenExpiration = tokenSplit[1] + '/' + tokenSplit[0] + '/' + tokenSplit[2];
            const tokenDate = Date.parse(tokenExpiration);
            ok = tokenDate <= this.convertDateToUTC(new Date()).getTime();
        }
        return ok;
    };
    convertDateToUTC = (date) => {
        return new Date(date.getUTCFullYear(), date.getUTCMonth(), date.getUTCDate(), date.getUTCHours(), date.getUTCMinutes(), date.getUTCSeconds());
    };
    /**
     * Creates an application message object depending on the received error code
     * @param {String} errorCode the error code received
     * @returns {Object} an error message object.
     */
    generateSSOErrorMessage = (errorCode) => {
        let errorObject = null;
        if (errorCode) {
            errorObject = {
                message: this.BaggagesSSOConstants.ERROR_MESSAGES[errorCode] || 'REST_SERVICE_ERROR_GENERAL',
                internalErrorCode: errorCode,
                errorCode: errorCode,
                needsTranslate: true
            };
        }
        return errorObject;
    };
    /**
     * Updates the SSO cookies with the new token and expiy times.
     * @param {Object} authResponse the response from the authorization service with the new tokens and its expiry times.
     */
    updateRefreshToken = (authResponse):void => {
        if (angular.isObject(authResponse)) {
            const expiration = new Date();
            expiration.setTime(expiration.getTime() + this.BaggagesSSOConstants.SSO_COOKIES.DEFAULT_EXPIRY_TIME);
            const extraCookiesInfo = { expires: expiration, path: this.BaggagesSSOConstants.SSO_COOKIES.DEFAULT_PATH };
            this.$cookies.put(this.BaggagesSSOConstants.SSO_COOKIES.LOGGED_TOKEN, authResponse.access_token, extraCookiesInfo);
            this.$cookies.put(this.BaggagesSSOConstants.SSO_COOKIES.LOGGED_TOKEN_EXPIRATION, this.generateTokenExpirationDateString(authResponse.expires_in), extraCookiesInfo);
            this.$cookies.put(this.BaggagesSSOConstants.SSO_COOKIES.LOGGED_REFRESH_TOKEN, authResponse.refresh_token, extraCookiesInfo);
            this.$cookies.put(this.BaggagesSSOConstants.SSO_COOKIES.LOGGED_REFRESH_TOKEN_EXPIRATION, this.generateTokenExpirationDateString(authResponse.refresh_expires_in), extraCookiesInfo);
        }
    };
    /**
     * Generates the expiration date in format DD/MM/YYYY HH:mm
     * @param {Integer} tokenExpirationSeconds the token expiration in seconds
     * @return {String} the token expiration in format DD/MM/YYYY HH:mm
     */
    generateTokenExpirationDateString = (tokenExpirationSeconds): string => {
        const expiryDate = new Date((new Date().getTime()) + (tokenExpirationSeconds * 1000));
        const day = (expiryDate.getUTCDate() < 10 ? '0' : '') + expiryDate.getUTCDate();
        const month = (expiryDate.getUTCMonth() < 9 ? '0' : '') + (expiryDate.getUTCMonth() + 1);
        const hour = (expiryDate.getUTCHours() < 10 ? '0' : '') + expiryDate.getUTCHours();
        const minutes = (expiryDate.getUTCMinutes() < 10 ? '0' : '') + expiryDate.getUTCMinutes();
        const seconds = (expiryDate.getUTCSeconds() < 10 ? '0' : '') + expiryDate.getUTCSeconds();
        return day + '/' + month + '/' + expiryDate.getUTCFullYear() + ' ' + hour + ':' + minutes + ':' + seconds;
    };
    /**
     * remove SSO localStorage and SSO cookies information
     */
    updateSSOStateToAnonymous = ():void => {
        this.localStorageService.remove('loggedToken');
        this.localStorageService.remove('refreshToken');
        this.localStorageService.remove('customerData');
        this.localStorageService.remove('userLogged');
        this.$rootScope.$broadcast('updateCustomerInfo');
        this.$cookies.remove(this.BaggagesSSOConstants.SSO_COOKIES.LOGGED_TOKEN, {path: this.BaggagesSSOConstants.SSO_COOKIES.DEFAULT_PATH});
        this.$cookies.remove(this.BaggagesSSOConstants.SSO_COOKIES.LOGGED_TOKEN_EXPIRATION, {path: this.BaggagesSSOConstants.SSO_COOKIES.DEFAULT_PATH});
        this.$cookies.remove(this.BaggagesSSOConstants.SSO_COOKIES.LOGGED_REFRESH_TOKEN, {path: this.BaggagesSSOConstants.SSO_COOKIES.DEFAULT_PATH});
        this.$cookies.remove(this.BaggagesSSOConstants.SSO_COOKIES.LOGGED_REFRESH_TOKEN_EXPIRATION, {path: this.BaggagesSSOConstants.SSO_COOKIES.DEFAULT_PATH});
    };
}