@Service('ib-http-services-search-module', ['$q', 'HttpUtilsService', 'CommonsHttpUtilsErrorService', 'CommonsGenericProxy'])

class BaggagesSearchService {

    public NUM_ATTEMPS: number;
    constructor (
        private $q: ng.IQService,
        private HttpUtilService: HttpUtilsService,
        private CommonsHttpUtilsErrorService: CommonsHttpUtilsErrorService,
        private CommonsGenericProxy: CommonsGenericProxy
    ) {
        this.NUM_ATTEMPS = 0;
    }

    getBaggages = (data: any) => {
        let defer = this.$q.defer();

        const getBaggagesSucces = (baggageData: any): void => {
            defer.resolve(baggageData.data);
            defer = null;
            this.NUM_ATTEMPS = 0;
        };

        const getBaggageCallback = () => {
            this.NUM_ATTEMPS++;
            this.getBaggages(data);
        };

        const getBaggagesError = (error: CommonsErrorsModel.ErrorDataGeneral): void => {
            this.CommonsHttpUtilsErrorService.controlError(error, this.NUM_ATTEMPS, getBaggageCallback, defer, false);
        };

        const endpoint = this.HttpUtilService.buildServiceEndpoint('ass-fass', 'BAGAGGE_CALCULATOR');
        this.CommonsGenericProxy.postGeneric(endpoint, data).then(getBaggagesSucces, getBaggagesError);
        return defer.promise;
    };
}